@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono", monospace, sans-serif;
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed", monospace, sans-serif;
  font-display: swap;
  src: url("../../fonts/RobotoCondensed-Bold.ttf") format("tff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed", monospace, sans-serif;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.tff") format("tff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", monospace, sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 25px;
  color: #f2f5f7;
  background-color: #00B2FF;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  font-family: "PT Mono", monospace, sans-serif;
  font-weight: 400;
  font-size: 16px;
  transition: opacity 0.3s ease;
  letter-spacing: 0.05em;
  border-radius: 3px;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1439px) {
  .btn {
    position: relative;
    left: 50px;
  }
}
@media (max-width: 767px) {
  .btn {
    position: static;
  }
}
.btn__description-tablet {
  display: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #00B2FF;
  color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal__close-btn-icon {
  fill: #ffffff;
}
.modal__title {
  font-weight: 400;
  font-size: 30px;
  line-height: 140.62%;
  margin: 0;
  margin-bottom: 5px;
}
.modal__description {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", monospace, sans-serif;
  margin: 0;
  margin-bottom: 40px;
}
.modal__input {
  background: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", monospace, sans-serif;
  color: #ffffff;
  width: 275px;
  height: 50px;
  padding-left: 15px;
  z-index: 5;
  width: 100%;
  margin-bottom: 20px;
}
.modal__input-textarea {
  height: 155px;
  padding-top: 10px;
}
.modal__input-textarea::placeholder {
  padding-top: 15px;
}
.modal__input::placeholder {
  color: #ffffff;
}
.modal__label {
  position: relative;
}
.modal__submit {
  border-radius: 3px;
  background-color: #041827;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  padding: 10px 65px;
  margin-top: 30px;
  width: 100%;
}
.modal__checkbox-description {
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  font-family: "PT Mono", monospace, sans-serif;
  color: #ffffff;
  display: inline-block;
  margin-top: 20px;
  left: 27px;
}
.modal__checkbox-description::before {
  width: 14.94px;
  height: 15px;
  position: absolute;
  content: "";
  border: 1px solid #ffffff;
  border-radius: 2px;
  left: -25px;
  background-repeat: no-repeat;
  background-position: center;
}
.modal__checkbox {
  top: 14px;
  left: 10px;
}
.modal__checkbox:checked + .modal__checkbox-description::before {
  background-image: url("../img/svg/sign.svg");
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header-container {
  display: flex;
  justify-content: space-evenly;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  color: #f2f5f7;
  position: fixed;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  align-items: center;
  z-index: 1000;
  padding-top: 30px;
  padding-bottom: 30px;
}
.header-container__media {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 52%;
}
.header-container__media-container {
  display: flex;
  gap: 12%;
  width: 100%;
}
.header-container__logo-wrapper {
  min-width: 245px;
}
.header-container__logo {
  float: left;
}
.header-container__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  white-space: nowrap;
  margin: 0;
  position: relative;
  left: 15px;
}
.header-container__title-description {
  white-space: nowrap;
  position: relative;
  left: 15px;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  bottom: 5px;
}
.header-container__media-wrapper {
  display: flex;
  align-items: center;
}
.header-container__media-description {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  font-family: "PT Mono", monospace, sans-serif;
  margin-left: 15px;
  white-space: nowrap;
}
.header-container__button {
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  width: 160px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 1023px) {
  .header-container {
    position: static;
    padding-top: 20px;
    padding-bottom: 25px;
    justify-content: space-around;
  }
  .header-container__media-container {
    justify-content: end;
  }
  .header-container__media-description {
    display: none;
  }
  .header-container__button {
    display: none;
  }
  .header-container__media {
    justify-content: end;
    margin: 0;
  }
  .header-container__media-wrapper {
    margin: 0;
    justify-content: end;
    width: auto;
    margin-right: 40px;
  }
}
@media (max-width: 767px) {
  .header-container {
    padding: 15px 35px 15px 25px;
    justify-content: space-between;
  }
  .header-container__media {
    position: relative;
    bottom: 3px;
  }
  .header-container__media-container {
    gap: 20%;
  }
  .header-container__media-wrapper {
    margin: 0;
  }
  .header-container__media-wrapper--phone {
    margin-left: 45px;
    margin-right: 10px;
  }
  .header-container__media-description {
    display: none;
  }
  .header-container__button {
    display: none;
  }
  .header-container__title {
    display: none;
  }
  .header-container__title-description {
    display: none;
  }
  .header-container__logo {
    width: 35px;
    height: 35px;
  }
  .header-container__logo-wrapper {
    min-width: auto;
  }
}
.footer-container {
  display: flex;
  background: #041827;
  color: #ffffff;
  padding-top: 50px;
  padding-bottom: 55px;
  justify-content: space-around;
}
.footer-container__wrapper {
  width: 245px;
}
.footer-container__logo {
  float: left;
}
.footer-container__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  white-space: nowrap;
  color: #f2f5f7;
  margin: 0;
  margin-left: 70px;
}
.footer-container__title-description {
  color: rgba(248, 253, 255, 0.7);
  white-space: nowrap;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin-left: 15px;
}
.footer-container__navigation-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer-container__social-list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 40px;
  display: flex;
  justify-content: space-around;
  width: 150px;
  align-items: center;
}
.footer-container__navigation-wrapper {
  display: flex;
  width: 470px;
  gap: 30px;
}
.footer-container__navigation-title {
  font-family: "Roboto Condensed", monospace, sans-serif;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
}
.footer-container__navigation-link {
  color: rgba(255, 255, 255, 0.7);
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  font-family: "PT Mono", monospace, sans-serif;
}
.footer-container__navigation-item:not(:last-child) {
  margin-bottom: 5px;
}
.footer-container__address-title {
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
}
.footer-container__address-list {
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  font-family: "PT Mono", monospace, sans-serif;
  color: rgba(255, 255, 255, 0.7);
  list-style: none;
  margin: 0;
  padding: 0;
}

@media (max-width: 1023px) {
  .footer-container {
    flex-wrap: wrap;
    padding: 50px;
  }
  .footer-container__wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 35px;
  }
  .footer-container__social-list {
    margin: 0;
  }
  .footer-container__navigation {
    margin-top: 35px;
  }
  .footer-container__navigation-wrapper {
    display: block;
    width: 300px;
  }
  .footer-container__address {
    margin-top: 35px;
  }
}
@media (max-width: 767px) {
  .footer-container {
    padding: 0;
  }
  .footer-container__wrapper {
    flex-direction: column;
  }
  .footer-container__social {
    margin-top: 20px;
  }
  .footer-container__navigation {
    width: 100%;
    padding: 20px;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    border-top: 1px solid rgba(248, 253, 255, 0.7);
  }
  .footer-container__navigation-title {
    margin: 0;
  }
  .footer-container__address {
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    margin: 0;
    margin-bottom: 35px;
    width: 100%;
    padding: 20px;
  }
  .footer-container__address-item {
    margin-top: 15px;
  }
  .footer-container__navigation-list:first-child {
    margin-top: 15px;
  }
  .footer-container__address-title {
    margin: 0;
  }
  .footer-container__footer-list--closed {
    animation: closeAnimationMobile 1s forwards;
    overflow: scroll;
    overflow-y: hidden;
  }
  .footer-container__footer-list--opened {
    display: block;
    animation: openAnimationMobile 1s forwards;
    height: auto;
    overflow: scroll;
